import './App.css';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import EN from './lang/en.json';
import Autocomplete from '@mui/material/Autocomplete';
import Typography from '@mui/material/Typography';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker  } from '@mui/x-date-pickers/DatePicker';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

function DateInput(props) {
    const { t, i18n } = useTranslation();
    const [data, setData] = React.useState(props.data);

    const handleChange = (newValue) =>  {
        console.log(props.data);
        console.log(newValue);
        props.func(newValue);
        setData(newValue);
    };

     return (
        <LocalizationProvider dateAdapter={AdapterDayjs}>
            <DatePicker 
                fullWidth
                id = {props.label}
                label={t('met.'+props.label)}
                value={props.disable ? null : data}
                disabled={props.disable}
                onChange={handleChange}
                format="YYYY/MM/DD"
            />
        </LocalizationProvider>
  );
}

function MetLevel(props) {
    const { t, i18n } = useTranslation();
    const [level, setLevel] = React.useState(props.data);

    const max = 100;
    const min = 0;

    const handleChange = (event) =>  {
        let value = event.target.value;
        console.log(value);
        value = Math.max(min, Math.min(max, Number(value)));
        if (!value) {
            setLevel('');
            props.func('');
        } else {
            setLevel(Number(value));
            props.func(Number(value));
        }
    };

     return (
        <TextField
            fullWidth
            id="metLevel"
            label={t('met.metLevel')}
            type="number"
            value={props.disable ? 1 : level}
            disabled={props.disable}
            onChange={handleChange}
        />
  );
}

function TextInput(props) {
    const { t, i18n } = useTranslation();
    const [data, setData] = React.useState(props.data);

    const handleChange = (event) =>  {
        let value = event.target.value;
        props.func(value);
        setData(value);
    };

     return (
        <TextField
            fullWidth
            id = {props.trainer+props.label}
            label={t('met.'+props.label)}
            value={data}
            onChange={handleChange}
        />
  );
}

function ValueInput(props) {
    const { t, i18n } = useTranslation();
    const [data, setData] = React.useState(props.data);
    const max = props.max;
    const min = 0;

    const handleChange = (event) =>  {
        let value = event.target.value;
        console.log(value);
        if (value === '') {
            props.func('');
            setData('');
        } else {
            value = Math.max(min, Math.min(max, Number(value)));
            props.func(Number(value));
            setData(Number(value));
        }
    };

     return (
        <TextField
            fullWidth
            id = {props.trainer+props.label}
            label={t('met.'+props.label)}
            type="number"
            value={data}
            onChange={handleChange}
        />
  );
}

function AsEgg(props) {
    const { t, i18n } = useTranslation();

    const [asEgg, setAsEgg] = React.useState(props.data);

     return (
        <FormControlLabel
            label={t('met.asEgg')}
            control={<Checkbox id="asEgg" 
                checked={asEgg}
                onChange={(event) => {
                    props.func(event.currentTarget.checked);
                    setAsEgg(event.currentTarget.checked);
                    props.setDisableInput(event.currentTarget.checked);
                }}
            />}
        />
  );
}

function TrainerGender(props) {
    const { t, i18n } = useTranslation();
    const [gender, setGender] = React.useState(props.data);
    const handleChange = (event) => {
        setGender(event.target.value);
        props.func(event.target.value);
    };
     return (
        <FormControl fullWidth>
            <InputLabel id={props.trainer+"-gender-select-label"}>{t('basic.gender')}</InputLabel>
            <Select
                labelId={props.trainer+"-gender-select-label"}
                id={props.trainer+"gender"}
                value={gender}
                label={t('basic.gender')}
                onChange={handleChange}
            >
                <MenuItem className='Bold' value={'M'} key={props.trainer+'-gender-select-option-M'}>{'♂'}</MenuItem>
                <MenuItem className='Bold' value={'F'} key={props.trainer+'-gender-select-option-F'}>{'♀'}</MenuItem>
            </Select>
        </FormControl>
  );
}

function Version(props) {
    const { t, i18n } = useTranslation();
    const [version, setVersion] = React.useState(props.data);
    const handleChange = (event) => {
        setVersion(event.target.value);
        props.func(event.target.value);
    };
     return (
        <FormControl fullWidth>
            <InputLabel id={"version-select-label"}>{t('met.version')}</InputLabel>
            <Select
                labelId={"version-select-label"}
                id={"version"}
                value={version}
                label={t('met.version')}
                onChange={handleChange}
            >
                <MenuItem className='Bold' value={50} key={'version-select-option-S'}>{t('met.versionS')}</MenuItem>
                <MenuItem className='Bold' value={51} key={'version-select-option-V'}>{t('met.versionV')}</MenuItem>
                <MenuItem className='Bold' value={''} key={'version-select-option-other'}>{t('met.versionOther')}</MenuItem>
            </Select>
        </FormControl>
  );
}

function AllLegalRibbon(props) {
    const { t, i18n } = useTranslation();

    const [data, setData] = React.useState(props.data);

     return (
        <FormControlLabel
            label={t('met.allLegalRibbon')}
            control={<Checkbox id="allLegalRibbon" 
                checked={data}
                onChange={(event) => {
                    props.func(event.currentTarget.checked);
                    setData(event.currentTarget.checked);
                }}
            />}
        />
  );
}

function ItemsSelect(props) { // for held item, nature, ball, tera type, language
    const { t, i18n } = useTranslation();
    const numItem = props.len;

    const [item, setItem] = React.useState(props.data);

    //console.log(numItem);
    let OptionList = [];

    for (let i = 0; i < numItem; i++) {
        OptionList.push({"label":t(props.label+'.'+i), "id":i});
    }

     return (
        <Autocomplete
        disablePortal
        id="mark"
        value={item === '' ? '' : t(props.label+'.'+item)}
        options={OptionList}
        onChange={(event, newValue) => {
            props.func(newValue ? newValue.id : '');
            setItem(newValue ? newValue.id : '');
        }}
        isOptionEqualToValue={(option, value) => (option.label === value) || value===''}
        renderInput={(params) => <TextField {...params} label={t('met.'+props.label)} />}
        />
  );
}

function Met(props) {
    const { t, i18n } = useTranslation();
    let met = props.met;
    let funcMap = props.funcMap;

    const [disableInput, setDisableInput] = React.useState(met.asEgg);
    
    return (
        <div>
            <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography className='Align-left'>{t('met.metDate')}</Typography>
                    </Grid>
                    <Grid item xs={2} className='MinWidth'>
                        <AsEgg data={met.asEgg} func={funcMap.setAsEgg} setDisableInput={setDisableInput} />
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <DateInput data={met.eggMetDate} func={funcMap.setEggMetDate} label={'eggMetDate'} disable={!disableInput} />
                    </Grid>
                    <Grid item xs={2} className='MinWidth'>
                        <MetLevel data={met.metLevel} func={funcMap.setMetLevel} disable={disableInput} />
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <DateInput data={met.metDate} func={funcMap.setMetDate} label={'metDate'} disable={false} />
                    </Grid>
                    <Grid item xs={12}>
                        <Typography className='Align-left'>{t('met.OT')}</Typography>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <TextInput data={met.OTTID} func={funcMap.setOTTID} label={'TID'} trainer={'OT'} />
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <TextInput data={met.OTSID} func={funcMap.setOTSID} label={'SID'} trainer={'OT'} />
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <TextInput data={met.OTname} func={funcMap.setOTname} label={'name'} trainer={'OT'} />
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <TrainerGender data={met.OTgender} func={funcMap.setOTgender} trainer={'OT'}/>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <ValueInput data={met.OTfriendship} max={255} trainer={'OT'} label={'friendship'} func={funcMap.setOTfriendship}/>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <Version data={met.version} func={funcMap.setVersion} />
                    </Grid>
                    <Grid item xs={12}>
                        <Typography className='Align-left'>{t('met.HT')}</Typography>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <TextInput data={met.HTname} func={funcMap.setHTname} label={'name'} trainer={'HT'} />
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <TrainerGender data={met.HTgender} func={funcMap.setHTgender} trainer={'HT'}/>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <ValueInput data={met.HTfriendship} max={255} trainer={'HT'} label={'friendship'} func={funcMap.setHTfriendship}/>
                    </Grid>
                    <Grid item xs={12} >
                        <Typography className='Align-left'>{t('met.ribbon')}</Typography>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <ItemsSelect data={met.mark} func={funcMap.setMark} label={'mark'} len={Object.keys(EN.mark).length}/>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <AllLegalRibbon data={met.allLegalRibbon} func={funcMap.setAllLegalRibbon} />
                    </Grid>
                </Grid>
            </Box>
        </div>
    )
}

export default Met;