import './App.css';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import pokeAttr from './data/pokeAttr.json';
import Stack from '@mui/material/Stack';
import Autocomplete from '@mui/material/Autocomplete';
import Typography from '@mui/material/Typography';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

function MoveInput(props) {
    const { t, i18n } = useTranslation();
    const [move, setMove] = React.useState(props.data);
    let OptionList = [];

    if (props.speciesId !== '') {
        let moveList = pokeAttr[parseInt(props.speciesId)][2];
        for (let i = 0; i < moveList.length; i++) {
            OptionList.push({"label":t('moves.'+moveList[i]), "id":moveList[i]});
        }
    }

     return (
        <Autocomplete
        fullWidth
        disablePortal
        id={'move'+props.idx}
        value={props.disable || move === '' ? '' : t('moves.'+move)}
        disabled={props.disable}
        options={OptionList}
        onChange={(event, newValue) => {
            props.func(newValue ? newValue.id : '');
            setMove(newValue ? newValue.id : '');
        }}
        isOptionEqualToValue={(option, value) => (option.label === value) || value===''}
        renderInput={(params) => <TextField {...params} label={t('basic.move')+props.idx} />}
        />
  );
}

function SuggestMoves(props) {
    const { t, i18n } = useTranslation();

    const [suggest, setSuggest] = React.useState(props.data);

     return (
        <FormControlLabel className='MinWidth'
            label={t('basic.suggestMoves')}
            control={<Checkbox id="suggestMoves" 
                checked={suggest}
                onChange={(event) => {
                    props.func(event.currentTarget.checked);
                    setSuggest(event.currentTarget.checked);
                    props.setDisableInput(event.currentTarget.checked);
                }}
            />}
        />
  );
}

function RelearnMoves(props) {
    const { t, i18n } = useTranslation();

    const [relearn, setRelearn] = React.useState(props.data);

     return (
        <FormControlLabel className='MinWidth'
            label={t('basic.relearnMoves')}
            control={<Checkbox id="relearnMoves" 
                checked={relearn}
                onChange={(event) => {
                    props.func(event.currentTarget.checked);
                    setRelearn(event.currentTarget.checked);
                }}
            />}
        />
  );
}

function Moves(props) {
    const { t, i18n } = useTranslation();
    let moves = props.moves;
    let funcMap = props.funcMap;

    const [disableInput, setDisableInput] = React.useState(moves.suggest);

    return (
        <div>
            <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Stack spacing={2} direction="row">
                            <SuggestMoves data={moves.suggested} func={funcMap.setSuggested} setDisableInput={setDisableInput} />
                            <RelearnMoves data={moves.relearn} func={funcMap.setRelearn} />
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack spacing={2} direction="row">
                            <Typography className='Align-center'>1.</Typography>
                            <MoveInput idx={1} data={moves.move1} func={funcMap.setMove1} speciesId={props.speciesId} disable={disableInput} />
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack spacing={2} direction="row">
                            <Typography className='Align-center'>2.</Typography>
                            <MoveInput idx={2} data={moves.move2} func={funcMap.setMove2} speciesId={props.speciesId} disable={disableInput} />
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack spacing={2} direction="row">
                            <Typography className='Align-center'>3.</Typography>
                            <MoveInput idx={3} data={moves.move3} func={funcMap.setMove3} speciesId={props.speciesId} disable={disableInput} />
                        </Stack>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack spacing={2} direction="row">
                            <Typography className='Align-center'>4.</Typography>
                            <MoveInput idx={4} data={moves.move4} func={funcMap.setMove4} speciesId={props.speciesId} disable={disableInput} />
                        </Stack>
                    </Grid>
                </Grid>
            </Box>
        </div>
    )
}

export default Moves;