function CustomTabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

CustomTabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function BasicTabs(props) {
  const [tabVal, setValue] = React.useState(0);
  const { t, i18n } = useTranslation();

  //const {pokemonSpecies, setSpecies} = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={tabVal} onChange={handleChange} aria-label="basic tabs example">
          <Tab label={t('tab.basic')} {...a11yProps(0)} />
          <Tab label={t('tab.stats')} {...a11yProps(1)} />
          <Tab label={t('tab.moves')} {...a11yProps(2)} />
          <Tab label={t('tab.met')} {...a11yProps(3)} />
        </Tabs>
      </Box>
      <CustomTabPanel value={tabVal} index={0}>
        <Basic speciesId={props.speciesId} setSpeciesId={props.setSpeciesId} basic={props.pokemon.Basic} funcMap={props.funcMap.Basic}/>
      </CustomTabPanel>
      <CustomTabPanel value={tabVal} index={1}>
        <Stats stats={props.pokemon.Stats} funcMap={props.funcMap.Stats}/>
      </CustomTabPanel>
      <CustomTabPanel value={tabVal} index={2}>
        <Moves speciesId={props.speciesId} moves={props.pokemon.Moves} funcMap={props.funcMap.Moves}/>
      </CustomTabPanel>
      <CustomTabPanel value={tabVal} index={3}>
        <Met met={props.pokemon.Met} funcMap={props.funcMap.Met}/>
      </CustomTabPanel>
    </Box>
  );
}

function Title(props) {
  const { t, i18n } = useTranslation();
  const [lang, setLang] = React.useState(localStorage.getItem('lang'));

  const handleChange = (event) => {
    i18n.changeLanguage(event.target.value);
    setLang(event.target.value);
    localStorage.setItem('lang', event.target.value);
  };
  return (
    <div className='Title'>
      <div className="App-header">
        <p>{t('home.title')}</p>
        <span className='warning' dangerouslySetInnerHTML={{ __html: t('home.warning') }}/>
      </div>
      <div className='LanguageSelector'>
        <Box sx={{ minWidth: 120 }}>
        <FormControl fullWidth>
          <InputLabel id="lang-select-label">{t('home.language')}</InputLabel>
          <Select
            labelId="lang-select-label"
            id="lang-select"
            label={t('home.language')}
            value={lang}
            onChange={handleChange}
          >
            <MenuItem value={'en'}>English</MenuItem>
            <MenuItem value={'chs'}>简体中文</MenuItem>
          </Select>
        </FormControl>
        </Box>
      </div>
    </div>
  )
}

