function pokemonToString() {
  let exportStr = "";
  /* species, gender, item */
  let basic = pokemon.Basic;
  if (basic.species !== '') {
    exportStr += EN.pokemon[basic.species] + " ";
  }
  if (basic.gender) {
    exportStr += "("+basic.gender+") "
  }
  if (basic.heldItem !== '') {
    exportStr += "@ "+EN.helditem[basic.heldItem];
  }
  exportStr += "\n";
  /* EVs */
  let evsList = [];
  let evs = pokemon.Stats.evs;
  if (evs.hp !== 0) {
    evsList.push(evs.hp+" HP")
  }
  if (evs.atk !== 0) {
    evsList.push(evs.atk+" Atk")
  }
  if (evs.def !== 0) {
    evsList.push(evs.def+" Def")
  }
  if (evs.spa !== 0) {
    evsList.push(evs.spa+" SpA")
  }
  if (evs.spd !== 0) {
    evsList.push(evs.spd+" SpD")
  }
  if (evs.spe !== 0) {
    evsList.push(evs.spe+" Spe")
  }
  if (evsList.length) {
    exportStr += "EVs: "+ evsList.join(" / ") + "\n";
  }
  /* IVs */
  let ivsList = [];
  let ivs = pokemon.Stats.ivs;
  if (ivs.hp !== 31) {
    ivsList.push(ivs.hp+" HP")
  }
  if (ivs.atk !== 31) {
    ivsList.push(ivs.atk+" Atk")
  }
  if (ivs.def !== 31) {
    ivsList.push(ivs.def+" Def")
  }
  if (ivs.spa !== 31) {
    ivsList.push(ivs.spa+" SpA")
  }
  if (ivs.spd !== 31) {
    ivsList.push(ivs.spd+" SpD")
  }
  if (ivs.spe !== 31) {
    ivsList.push(ivs.spe+" Spe")
  }
  if (ivsList.length) {
    exportStr += "IVs: "+ ivsList.join(" / ") + "\n";
  }
  //ability
  if (basic.ability !== '') {
    exportStr += "Ability: "+EN.abilities[basic.ability]+" \n";
  }
  //teratype
  if (basic.teraType !== '') {
    exportStr += "Tera Type: "+EN.teraType[basic.teraType]+" \n";
  }
  //nature
  if (basic.nature !== '') {
    exportStr += EN.nature[basic.nature]+" Nature \n";
  }
  //Ball
  if (basic.ball !== '') {
    exportStr += "Ball: "+EN.ball[basic.ball]+" \n";
  }
  //Shiny
  if (basic.shiny) {
    exportStr += "Shiny: Yes \n";
  }
  //language
  if (basic.language !== '') {
    exportStr += "Language: "+EN.lang[basic.language]+" \n";
  }
  //OTname
  let met = pokemon.Met;
  if (met.OTname !== '') {
    exportStr += "OT: "+met.OTname+"\n";
  }
  //OTgender
  if (met.OTgender !== '') {
    exportStr += "OTGender: "+(met.OTgender === 'M' ? "Male" : "Female")+"\n";
  }
  //OTTID
  if (met.OTTID !== '') {
    exportStr += "TID: "+met.OTTID+"\n";
  }
  //OTSID
  if (met.OTSID !== '') {
    exportStr += "SID: "+met.OTSID+"\n";
  }
  //Met level
  if (!met.asEgg && met.metLevel !== '') {
    exportStr += "=Met_Level="+met.metLevel+"\n";
  }
  //nickname
  if (basic.nickname !== '') {
    exportStr += ".IsNicknamed=true \n.Nickname="+basic.nickname+"\n";
  }
  //size
  if (basic.size !== '') {
    exportStr += ".Scale="+basic.size+"\n";
  }
  //version
  if (met.version !== '') {
    exportStr += ".Version="+met.version+"\n";
  }
  //met date
  if (met.metDate) {
    exportStr += ".MetDate="+met.metDate.format('YYYYMMDD')+"\n";
  }
  //egg met date
  if (met.asEgg && met.eggMetDate) {
    exportStr += ".EggMetDate="+met.eggMetDate.format('YYYYMMDD')+"\n";
  }
  //OT friendship
  if (met.OTfriendship !== '') {
    exportStr += ".OT_Friendship="+met.OTfriendship+"\n";
  }
  //HT name
  if (met.HTname !== '') {
    exportStr += ".HT_Name="+met.HTname+"\n";
  }
  //HT friendship
  if (met.HTfriendship !== '') {
    exportStr += ".HT_Friendship="+met.HTfriendship+"\n";
  }
  //HT gender
  if (met.HTgender !== '') {
    exportStr += ".HT_Gender="+(met.HTgender === 'M' ? 0 : 1)+"\n";
  }
  //relearn moves
  let moves = pokemon.Moves;
  if (moves.relearn) {
    exportStr += ".RelearnMoves=$suggestAll\n";
  }
  //ribbons
  if (met.allLegalRibbon) {
    exportStr += ".Ribbons=$suggestAll\n";
  }
  //mark
  if (met.mark !== '') {
    let markStr = EN.mark[met.mark];
    markStr = markStr.replace('-', ' ');
    let markArr = markStr.split(' ');
    markArr.pop();
    markStr = markArr.join('');
    exportStr += ".RibbonMark"+markStr+"=True\n";
  }
  //moves
  if (moves.suggested) {
    exportStr += ".Moves=$suggest\n";
  } else {
    if (moves.move1 !== '') {
      exportStr += "- "+EN.moves[moves.move1]+"\n";
    }
    if (moves.move2 !== '') {
      exportStr += "- "+EN.moves[moves.move2]+"\n";
    }
    if (moves.move4 !== '') {
      exportStr += "- "+EN.moves[moves.move3]+"\n";
    }
    if (moves.move4 !== '') {
      exportStr += "- "+EN.moves[moves.move4]+"\n";
    }
  }
  console.log(exportStr);
  return exportStr;
}

