
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import chs from './chs.json';
import en from './en.json';
 
const resources = {
  en: {
    translation: en
  },
  chs: {
    translation: chs
  }
};

var lang = localStorage.getItem('lang');
if (!lang) lang = 'en';
 
i18n.use(initReactI18next).init({
  resources,
  lng: lang,
  interpolation: {
    escapeValue: false
  }
});
 
export default i18n;