import './App.css';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';

function ValueInput(props) {
    const { t, i18n } = useTranslation();
    const [data, setData] = React.useState(props.data);
    const max = props.max;
    const min = 0;

    const handleChange = (event) =>  {
        let value = event.target.value;
        console.log(value);
        if (value === '') {
            props.func(props.default);
            setData('');
        } else {
            value = Math.max(min, Math.min(max, Number(value)));
            props.func(Number(value));
            setData(Number(value));
        }
    };

     return (
        <TextField
            fullWidth
            id = {props.label+props.col}
            label={t('stats.'+props.label)}
            type="number"
            value={data}
            onChange={handleChange}
            InputLabelProps={{
                shrink: true,
          }}
        />
  );
}

function Stats(props) {
    const { t, i18n } = useTranslation();
    let stats = props.stats;
    let funcMap = props.funcMap;
    return (
        <div>
            <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={3}>
                    <Grid item xs={6} className='Align-center'>
                        <Typography>{t('stats.ivs')}</Typography>
                    </Grid>
                    <Grid item xs={6} className='Align-center'>
                        <Typography>{t('stats.evs')}</Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.ivs.hp} max={31} col={'iv'} label={'hp'} func={funcMap.setIvHp} default={31}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.evs.hp} max={252} col={'ev'} label={'hp'} func={funcMap.setEvHp} default={0}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.ivs.atk} max={31} col={'iv'} label={'atk'} func={funcMap.setIvAtk} default={31}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.evs.atk} max={252} col={'ev'} label={'atk'} func={funcMap.setEvAtk} default={0}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.ivs.def} max={31} col={'iv'} label={'def'} func={funcMap.setIvDef} default={31}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.evs.def} max={252} col={'ev'} label={'def'} func={funcMap.setEvDef} default={0}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.ivs.spa} max={31} col={'iv'} label={'spa'} func={funcMap.setIvSpa} default={31}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.evs.spa} max={252} col={'ev'} label={'spa'} func={funcMap.setEvSpa} default={0}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.ivs.spd} max={31} col={'iv'} label={'spd'} func={funcMap.setIvSpd} default={31}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.evs.spd} max={252} col={'ev'} label={'spd'} func={funcMap.setEvSpd} default={0}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.ivs.spe} max={31} col={'iv'} label={'spe'} func={funcMap.setIvSpe} default={31}/>
                    </Grid>
                    <Grid item xs={6}>
                        <ValueInput data={stats.evs.spe} max={252} col={'ev'} label={'spe'} func={funcMap.setEvSpe} default={0}/>
                    </Grid>
                </Grid>
            </Box>
        </div>
    )
}

export default Stats;