import './App.css';
import * as React from 'react';
import { useTranslation } from 'react-i18next';
import pokeAttr from './data/pokeAttr.json';
import EN from './lang/en.json';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

function Species(props) {
    const numPoke = pokeAttr.length;
    const { t, i18n } = useTranslation();
    let OptionList = [];

    for (let i = 0; i < numPoke; i++) {
        OptionList.push({"label":t('pokemon.'+i), "id":i});
    }

     return (
        <Autocomplete
        disablePortal
        id="species"
        value={props.speciesId === '' ? '' : t('pokemon.'+props.speciesId)}
        options={OptionList}
        onChange={(event, newValue) => {
          props.setSpeciesId(newValue ? newValue.id : '');
          props.func(newValue ? newValue.id : '');
        }}
        isOptionEqualToValue={(option, value) => (option.label === value) || value===''}
        renderInput={(params) => <TextField {...params} label={t('basic.species')} />}
        />
  );
}

function Ability(props) {
    const { t, i18n } = useTranslation();
    const [ability, setAbility] = React.useState(props.data);
    let OptionList = [];

    if (props.speciesId !== '') {
        let abilityList = pokeAttr[parseInt(props.speciesId)][1];
        let abilityUnique = [...new Set(abilityList)];
        for (let i = 0; i < abilityUnique.length; i++) {
            OptionList.push({"label":t('abilities.'+abilityUnique[i]), "id":abilityUnique[i]});
        }
    }

     return (
        <Autocomplete
        disablePortal
        id="ability"
        value={ability === '' ? '' : t('abilities.'+ability)}
        options={OptionList}
        onChange={(event, newValue) => {
            props.func(newValue ? newValue.id : '');
            setAbility(newValue ? newValue.id : '');
        }}
        isOptionEqualToValue={(option, value) => (option.label === value) || value===''}
        renderInput={(params) => <TextField {...params} label={t('basic.ability')} />}
        />
  );
}

function Gender(props) {
    const { t, i18n } = useTranslation();
    let OptionList = [];
    const [gender, setGender] = React.useState(props.data);
    const handleChange = (event) => {
        setGender(event.target.value);
        props.func(event.target.value);
    };
    if (props.speciesId !== '') {
        let genderIdx = pokeAttr[parseInt(props.speciesId)][0];
        if (genderIdx === 0) {
            OptionList.push({value:'0', label:'♂'});
        } else if (genderIdx === 254) {
            OptionList.push({value:'0', label:'♀'});
        } else if (genderIdx === 255) {
            OptionList.push({value:'0', label:''});
        } else {
            OptionList.push({value:'M', label:'♂'});
            OptionList.push({value:'F', label:'♀'});
        }
        
    }

     return (
        <FormControl fullWidth>
            <InputLabel id="gender-select-label">{t('basic.gender')}</InputLabel>
            <Select
                labelId="gender-select-label"
                id="gender"
                value={gender}
                label={t('basic.gender')}
                onChange={handleChange}
            >
                {OptionList.map(({ value, label }, index) => <MenuItem className='Bold' value={value} key={'gender-select-option-'+value}>{label}</MenuItem>)}
            </Select>
        </FormControl>
  );
}

function Level(props) {
    const { t, i18n } = useTranslation();
    const [level, setLevel] = React.useState(props.data);

    const max = 100;
    const min = 0;

    const handleChange = (event) =>  {
        let value = event.target.value;
        console.log(value);
        value = Math.max(min, Math.min(max, Number(value)));
        if (!value) {
            setLevel('');
            props.func('');
        } else {
            setLevel(Number(value));
            props.func(Number(value));
        }
    };

     return (
        <TextField
            fullWidth
            id="level"
            label={t('basic.level')}
            type="number"
            value={level}
            onChange={handleChange}
        />
  );
}

function Size(props) {
    const { t, i18n } = useTranslation();
    const [size, setSize] = React.useState(props.data);

    const max = 255;
    const min = 0;

    const handleChange = (event) =>  {
        let value = event.target.value;
        console.log(value);
        if (value === '') {
            setSize('');
            props.func('');
        } else {
            value = Math.max(min, Math.min(max, Number(value)));
            setSize(Number(value));
            props.func(Number(value));
        }
    };

     return (
        <TextField
            fullWidth
            id="size"
            label={t('basic.size')}
            type="number"
            value={size}
            onChange={handleChange}
        />
  );
}

function Shiny(props) {
    const { t, i18n } = useTranslation();

    const [isShiny, setIsShiny] = React.useState(props.shiny);

     return (
        <FormControlLabel
            label={t('basic.shiny')}
            control={<Checkbox id="shiny" 
                checked={isShiny}
                onChange={(event) => {
                    props.func(event.currentTarget.checked);
                    setIsShiny(event.currentTarget.checked);
                }}
            />}
        />
  );
}

function ItemsSelect(props) { // for held item, nature, ball, tera type, language
    const { t, i18n } = useTranslation();
    const numItem = props.len;

    const [item, setItem] = React.useState(props.data);

    //console.log(numItem);
    let OptionList = [];

    for (let i = 0; i < numItem; i++) {
        OptionList.push({"label":t(props.label+'.'+i), "id":i});
    }

     return (
        <Autocomplete
        disablePortal
        id={t('basic.'+props.label)}
        value={item === '' ? '' : t(props.label+'.'+item)}
        options={OptionList}
        onChange={(event, newValue) => {
            props.func(newValue ? newValue.id : '');
            setItem(newValue ? newValue.id : '');
        }}
        isOptionEqualToValue={(option, value) => (option.label === value) || value===''}
        renderInput={(params) => <TextField {...params} label={t('basic.'+props.label)} />}
        />
  );
}

function TextInput(props) {
    const { t, i18n } = useTranslation();
    const [data, setData] = React.useState(props.data);

    const handleChange = (event) =>  {
        let value = event.target.value;
        props.func(value);
        setData(value);
    };

     return (
        <TextField
            fullWidth
            id = {props.label}
            label={t('basic.nickname')}
            value={data}
            onChange={handleChange}
        />
  );
}

function Basic(props) {
    let basic = props.basic;
    let funcMap = props.funcMap;
    return (
        <div>
            <Box sx={{ flexGrow: 1 }}>
                <Grid container spacing={3}>
                    <Grid item xs={4} className='MinWidth'>
                        <Species speciesId={props.speciesId} setSpeciesId={props.setSpeciesId} func={props.funcMap.setSpecies}/>
                    </Grid>
                    <Grid item xs={1.5} className='MinWidth'>
                        <Shiny shiny={basic.shiny} func={funcMap.setShiny}/>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <ItemsSelect data={basic.heldItem} func={funcMap.setHeldItem} label={'helditem'} len={Object.keys(EN.helditem).length}/>
                    </Grid>
                    <Grid item xs={2.5} className='MinWidth'>
                        <TextInput data={basic.nickname} func={funcMap.setNickname} />
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <Ability speciesId={props.speciesId} data={basic.ability} func={funcMap.setAbility}/>
                    </Grid>
                    <Grid item xs={3} className='MinWidth'>
                        <Gender speciesId={props.speciesId} data={basic.gender} func={funcMap.setGender}/>
                    </Grid>
                    <Grid item xs={2} className='MinWidth'>
                        <Level data={basic.level} func={funcMap.setLevel}/>
                    </Grid>
                    <Grid item xs={3} className='MinWidth'>
                        <ItemsSelect data={basic.nature} func={funcMap.setNature} label={'nature'} len={Object.keys(EN.nature).length}/>
                    </Grid>
                    <Grid item xs={3} className='MinWidth'>
                    <ItemsSelect data={basic.ball} func={funcMap.setBall} label={'ball'} len={Object.keys(EN.ball).length}/>
                    </Grid>
                    <Grid item xs={3} className='MinWidth'>
                        <ItemsSelect data={basic.teraType} func={funcMap.setTeraType} label={'teraType'} len={Object.keys(EN.teraType).length}/>
                    </Grid>
                    <Grid item xs={2} className='MinWidth'>
                        <Size data={basic.size} func={funcMap.setSize}/>
                    </Grid>
                    <Grid item xs={4} className='MinWidth'>
                        <ItemsSelect data={basic.language} func={funcMap.setLanguage} label={'lang'} len={Object.keys(EN.lang).length}/>
                    </Grid>
                </Grid>
            </Box>
        </div>
    )
}

export default Basic;